* {
  box-sizing: border-box; }

html, body {
  font-family: 'Open Sans'; }

.container {
  max-width: 1000px;
  margin: auto;
  padding: 0.5em; }
  .container [class*="grid-"] {
    float: left;
    padding: 0.5em; }
  .container .grid__content {
    border: 1px #eee solid;
    padding: 0.5em; }
  .container .grid__content input[type="text"] {
    width: 100%; }
  .container .grid-1 {
    width: 8.33333%; }
  .container .grid-2 {
    width: 16.66667%; }
  .container .grid-3 {
    width: 25%; }
  .container .grid-4 {
    width: 33.33333%; }
  .container .grid-5 {
    width: 41.66667%; }
  .container .grid-6 {
    width: 50%; }
  .container .grid-7 {
    width: 58.33333%; }
  .container .grid-8 {
    width: 66.66667%; }
  .container .grid-9 {
    width: 75%; }
  .container .grid-10 {
    width: 83.33333%; }
  .container .grid-11 {
    width: 91.66667%; }
  .container .grid-12 {
    width: 100%; }
  .container:after {
    content: "";
    display: table;
    clear: both; }

.floatl {
  float: left; }

.floatr {
  float: right; }

.txt-center {
  text-align: center; }

.txt-right {
  text-align: right; }

[data-hidden] {
  display: none; }

.error {
  background-color: #FF6363;
  border: 1px #E64242 solid; }

.list {
  list-style: none; }
  .list__item {
    position: relative;
    padding: 0.5em;
    margin: 0.5em 0;
    border: 1px #e3e2de solid;
    background-color: #eee; }
    .list__item:hover {
      cursor: pointer;
      border-color: #aaa; }
    .list__item--del {
      position: absolute;
      right: 0;
      padding-right: 0.5em; }
    .list__item--selected {
      border: 1px #aaa solid;
      font-weight: 700; }

.grid__content h1 {
  font-size: 1.5em; }

[data-toggle] {
  float: right;
  min-width: 20px; }
